*{margin:0;padding:0;box-sizing:border-box}body{font-family:'Lato', sans-serif;font-size:16px;background:white}.contenedor{width:90%;max-width:1280px;margin:0 auto;overflow:hidden}header{width:100%;height:600px;background-image:url(/src/assets/images/img/portada.png);background-repeat:no-repeat;background-position:center}header .menu{display:flex;justify-content:flex-end;margin:20px 0}header .menu a{display:inline-block;padding:15px 10px;font-size:18px;text-decoration:none;color:white;font-weight:300}header .menu a:hover{text-decoration:underline}header .contenedor-texto-header{margin:150px 0;text-align:center}header .contenedor-texto-header .titulo-header{font-size:80px;color:white;margin-bottom:5px}header .contenedor-texto-header .linea{width:600px;margin:0 auto;height:3px;background:dodgerblue;margin-bottom:20px}header .contenedor-texto-header .subtitulo-header{font-size:20px;color:white;font-weight:300;margin-bottom:20px}header .contenedor-texto-header .action{display:inline-block;padding:15px 15px;border:solid dodgerblue;text-align:center;color:white;text-decoration:none;border-radius:3px;margin-top:20px}header .contenedor-texto-header .action:hover{background:dodgerblue;color:white}main .acerca-de{background:#e4e4e4;padding:45px 0;text-align:center}main .acerca-de .contenedor-texto-acerca-de .titulo-acerca-de{font-size:24px;font-weight:900;margin-bottom:30px}main .acerca-de .contenedor-texto-acerca-de .parrafo-acerca-de{color:grey;font-weight:600px;margin-bottom:30px}main .icono{padding:30px 0}.contenedor-icono{display:flex;justify-content:space-between;text-align:center}.contenedor-icono .icono-individual{width:33%;margin:20px 0}.contenedor-icono .icono-individual h3{margin:10px 0}main .portafolio{background:#e4e4e4;padding:60px 0}main .contenedor-texto-portafolio{text-align:center}main .contenedor-texto-portafolio .titulo-portafolio{font-weight:900;font-size:28px;margin-bottom:20px}main .contenedor-texto-portafolio .parrafo-portafolio{font-weight:normal;color:grey;margin-bottom:60px}main .portafolio .contenedor-portafolio{display:flex;flex-wrap:wrap;justify-content:space-between}main .portafolio .contenedor-portafolio img{width:32%;margin:0 auto;margin-bottom:10px;border-radius:5px}main .portafolio .contenedor-portafolio img:hover{opacity:.6}footer{width:100%;height:600px;padding:60px 0;background-image:url(/src/assets/images/img/footer.png)}footer .footer-texto{text-align:center;color:white}footer .footer-texto .titulo-footer{font-size:38px;font-weight:900;margin-bottom:15px}footer .footer-texto .parrafo-footer{font-weight:300;color:grey;margin-bottom:45px}footer .formulario{display:flex;flex-direction:column;width:80%;margin:0 auto}footer .formulario .nombre-contacto,footer .formulario .nombre-correo,footer .formulario .mensaje{display:inline-block;padding:15px 20px;margin-bottom:20px;background:none;border:1px solid grey;color:white;border-radius:3px;font-family:'lato';font-weight:300;font-size:14px}footer .formulario .mensaje{max-width:100%;min-width:100%;min-height:150px;max-height:150px}footer .formulario .derecha{display:flex;justify-content:flex-end}footer .formulario .boton-envio{display:inline-block;padding:10px 20px;background:none;border:1px solid dodgerblue;color:white;border-radius:3px}footer .formulario .boton-envio:hover{background:dodgerblue;color:white;cursor:pointer}@media screen and (max-width: 600px){header .menu{justify-content:center;flex-wrap:wrap}header .contenedor-texto-header{margin:100px 0}header .contenedor-texto-header .titulo-header{font-size:60px}}@media screen and (max-width: 550px){.contenedor-icono{flex-direction:column}.contenedor-icono .icono-individual{width:100%;margin:0 auto}main .portafolio .contenedor-portafolio{justify-content:space-around}main .portafolio .contenedor-portafolio img{width:48%}}
